.moving-image {
    position: relative;
    transition: transform 0.3s ease; /* Optional: smooth transition when hover ends */
  }
  
  .moving-image:hover {
    animation: rotateClockwise 2s infinite ease-in-out;
  }
  
  .moving-image.reverse:hover {
    animation: rotateCounterClockwise 2s infinite ease-in-out;
  }
  
  @keyframes rotateClockwise {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(40deg); /* Rotate 90 degrees clockwise */
    }
    100% {
      transform: rotate(0deg); /* Return to original position */
    }
  }
  
  @keyframes rotateCounterClockwise {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(-90deg); /* Rotate 90 degrees counterclockwise */
    }
    100% {
      transform: rotate(0deg); /* Return to original position */
    }
  }
  